import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { InstituteModel } from 'src/app/shared/models/model';
import { InstitutionService } from 'src/app/shared/services/institution.service';
import { ToastrService } from 'src/app/shared/services/toastr-message.service';

@Component({
  selector: 'app-institution',
  templateUrl: './institution.component.html',
  styleUrls: ['./institution.component.scss']
})
export class InstitutionComponent implements OnInit {
  institutionForm!: FormGroup;
  institutions: InstituteModel[] = [];
  display: boolean = false

  constructor(private fb: FormBuilder, private institutionService: InstitutionService, private toastrService: ToastrService) {
    this.institutionForm = this.fb.group({
      id: 0,
      name: ['', Validators.required],
      addres: ['', Validators.required],
    });
  }

  ngOnInit() {
    this.getInstitution();
  }

  getInstitution() {
    this.institutionService.get().subscribe((response: any) => {
      this.institutions = response;
    });
  }
  updateData() {
    let temp = [...this.institutions];
    temp = JSON.parse(JSON.stringify(temp));
    this.institutions = temp;
  }

  showDialog() {
    this.display = true;
    this.institutionForm.reset();
  }

  add() {
    const data = this.institutionForm.value;
    if (!data.id) {
      delete data.id;
    }

    this.institutionService.create(data).subscribe({
      next: (response: any) => {
        if (!!response.id) {
          this.institutions.push(response);
          this.updateData();
          this.toastrService.success('Institutions Create Successfully.');
          this.display = false;
        }
      },
      error: () => {
        this.toastrService.error('Institutions Create Failed.');
      }
    });
  }

  edit() {
    this.institutionService.update(this.institutionForm.value).subscribe((response: any) => {
      // if (!!response.id) {
      const data = this.institutionForm.value;
      const index = this.institutions.findIndex(i => i.id === data.id);
      this.institutions[index] = data;
      this.toastrService.success('Institutions Updated Successfully.');
      this.display = false;
      // }
    });
  }

  onSubmit() {
    if (this.institutionForm.invalid) {
      return;
    }

    if (!this.institutionForm.value.id) {
      this.add();
    } else {
      this.edit();
    }
  }

  delete(id: number) {
    this.institutionService.delete(id).subscribe((response: any) => {
      const index = this.institutions.findIndex(i => i.id === id);
      this.institutions.splice(index, 1);
      this.toastrService.success('Institutions Deleted Successfully.');
    });
  }

  fillForm(institution: InstituteModel) {
    this.display = true;
    this.institutionForm.patchValue(institution);
  }

  public close(): void {
    window.alert('Are u Sure Leave From');
    this.display = false;
  }
}
